@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .bg-btn-primary {
    @apply from-[#345FF8] to-[#703EFF] bg-gradient-97;
  }
  .text-btn-primary {
    @apply text-transparent from-[#345FF8] to-[#703EFF] bg-gradient-97 bg-clip-text;
  }
  .btn {
    @apply text-L1 bg-btn-primary py-3 px-6 md:py-4 md:px-10   rounded-full;
  }

  .table-item {
    @apply md:py-4 md:px-6 md:min-w-[180px] py-3 px-3 min-w-[100px];
  }

  .btn-sm {
    @apply text-L1 bg-btn-primary px-4 py-2 rounded-xl;
  }
  .h1 {
    @apply font-bold text-[40px] leading-[48px];
  }
  .h2 {
    @apply font-medium text-[32px] leading-[38.4px];
  }
  .h3 {
    @apply font-normal text-[24px] leading-[35.8px];
  }
  .h4 {
    @apply font-normal text-[20px] leading-[24px];
  }
  .h5 {
    @apply font-normal text-[16px] leading-[19.2px];
  }
}

@layer base {
  html {
    font-family: "Noto Sans TC", "sans-serif";
    background-color: #efefef;
  }
}
